.statistics {
  padding-top: 40px;

  text-align: center;
}

.title {
  background-color: #ffffff;
  padding: 15px 5px;
  font-weight: normal;
  color: grey;
  font-size: 15px;
  line-height: 1.5;
  margin: 0;
}

.statList {
  display: flex;
}

.item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  min-height: 60px;
  color: #ffffff;
}

.label {
  font-size: 12px;
  line-height: 1.3;
  font-weight: normal;
}

.percentage {
  font-size: 16px;
  line-height: 1.6;
  font-weight: 500;
}
